

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ElementRef,
  Input,
  OnChanges,
  OnInit,
  Renderer2,
  SimpleChanges,
  ViewEncapsulation
} from '@angular/core';
import { toCssPixel } from 'tds-ui/shared/utility';

import { TDSSkeletonAvatar, TDSSkeletonAvatarShape, TDSSkeletonAvatarSize, TDSSkeletonParagraph, TDSSkeletonTitle } from './skeleton.type';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-skeleton',
  exportAs: 'tdsSkeleton',
  host: {
    'class':"table w-full box-border", 
    '[class.tds-skeleton-with-avatar]': '!!tdsAvatar',
    '[class.tds-skeleton-active]': 'tdsActive',
    '[class.animate-pulse]': 'tdsActive',
    '[class.tds-skeleton-round]': '!!tdsRound'
  },
  template: `
    <ng-container *ngIf="tdsLoading">
      <div class="tds-skeleton-header table-cell pr-4 align-top" *ngIf="!!tdsAvatar">
        <tds-skeleton-element tdsType="avatar" [tdsSize]="avatar.size || 'md'" [tdsShape]="avatar.shape || 'circle'"></tds-skeleton-element>
      </div>
      <div class="tds-skeleton-content w-full table-cell align-top m-0 p-0">
        <h3 *ngIf="!!tdsTitle" class="tds-skeleton-title w-full h-[18px] mt-4 bg-neutral-2-100 dark:bg-d-neutral-2-200 rounded-10" [style.width]="toCSSUnit(title.width)"></h3>
        <ul *ngIf="!!tdsParagraph" class="tds-skeleton-paragraph mt-4 w-full">
          <li class="w-full h-[14px] bg-neutral-2-100 dark:bg-d-neutral-2-200 rounded-10 mb-2.5" *ngFor="let row of rowsList; let i = index" [style.width]="toCSSUnit(widthList[i])"></li>
        </ul>
      </div>
    </ng-container>
    <ng-container *ngIf="!tdsLoading">
      <ng-content></ng-content>
    </ng-container>
  `
})
export class TDSSkeletonComponent implements OnInit, OnChanges {
  @Input() tdsActive = false;
  @Input() tdsLoading = true;
  @Input() tdsRound = false;
  @Input() tdsTitle: TDSSkeletonTitle | boolean = true;
  @Input() tdsAvatar: TDSSkeletonAvatar | boolean = false;
  @Input() tdsParagraph: TDSSkeletonParagraph | boolean = true;

  title!: TDSSkeletonTitle;
  avatar!: TDSSkeletonAvatar;
  paragraph!: TDSSkeletonParagraph;
  rowsList: number[] = [];
  widthList: Array<number | string> = [];

  constructor(private cdr: ChangeDetectorRef, renderer: Renderer2, elementRef: ElementRef) {
    renderer.addClass(elementRef.nativeElement, 'tds-skeleton');
  }

  toCSSUnit(value: number | string = ''): string {
    return toCssPixel(value);
  }

  private getTitleProps(): TDSSkeletonTitle {
    const hasAvatar: boolean = !!this.tdsAvatar;
    const hasParagraph: boolean = !!this.tdsParagraph;
    let width = '';
    if (!hasAvatar && hasParagraph) {
      width = '38%';
    } else if (hasAvatar && hasParagraph) {
      width = '50%';
    }
    return { width, ...this.getProps(this.tdsTitle) };
  }

  private getAvatarProps(): TDSSkeletonAvatar {
    const shape: TDSSkeletonAvatarShape = !!this.tdsTitle && !this.tdsParagraph ? 'square' : 'circle';
    const size: TDSSkeletonAvatarSize = 'md';
    return { shape, size, ...this.getProps(this.tdsAvatar) };
  }

  private getParagraphProps(): TDSSkeletonParagraph {
    const hasAvatar: boolean = !!this.tdsAvatar;
    const hasTitle: boolean = !!this.tdsTitle;
    const basicProps: TDSSkeletonParagraph = {};
    // Width
    if (!hasAvatar || !hasTitle) {
      basicProps.width = '61%';
    }
    // Rows
    if (!hasAvatar && hasTitle) {
      basicProps.rows = 3;
    } else {
      basicProps.rows = 2;
    }
    return { ...basicProps, ...this.getProps(this.tdsParagraph) };
  }

  private getProps<T>(prop: T | boolean | undefined): T | {} {
    return prop && typeof prop === 'object' ? prop : {};
  }

  private getWidthList(): Array<number | string> {
    const { width, rows } = this.paragraph;
    let widthList: Array<string | number> = [];
    if (width && Array.isArray(width)) {
      widthList = width;
    } else if (width && !Array.isArray(width)) {
      widthList = [];
      widthList[rows! - 1] = width;
    }
    return widthList;
  }

  private updateProps(): void {
    this.title = this.getTitleProps();
    this.avatar = this.getAvatarProps();
    this.paragraph = this.getParagraphProps();
    this.rowsList = [...Array(this.paragraph.rows)];
    this.widthList = this.getWidthList();
    this.cdr.markForCheck();
  }

  ngOnInit(): void {
    this.updateProps();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.tdsTitle || changes.tdsAvatar || changes.tdsParagraph) {
      this.updateProps();
    }
  }
}
